@font-face {
  font-family: "ONE-Mobile-Title";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-Title.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.work_img {
  width: 90%;
  height: 80%;
  margin: 0 auto;
  text-align: center;
}
.work_img img {
  width: 90%;
  height: 540px;
  object-fit: fill;

  margin: 0 auto;
}
.work_object {
  height: 650px;
  width: 90%;
  margin: 0 auto;
}
#multiple_main {
  margin-top: 60px;
}

#multiple_main .slick-list {
  position: relative;

  display: block;
  overflow: hidden;

  margin: 0;
  padding: 0;

  width: 80%;
  height: 600px;
  margin: 0 auto;
}

#multiple_main .slick-prev:before {
  font-size: 30px;

  color: rgb(0, 0, 0);
}
#multiple_main .slick-next:before {
  font-size: 30px;

  color: rgb(0, 0, 0);
}

#multiple_main .slick-dots {
  bottom: 14px;
}

#multiple_main .slick-next {
  right: 5%;
}
#multiple_main .slick-prev {
  left: 5%;
}

.work_txt {
  font-family: "ONE-Mobile-Title";
  position: relative;
  top: -50px;
  text-align: center;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.589);
  color: white;
  font-size: 120%;
}
.work_txt div {
  line-height: 47px;
}

.work_hover:not(.link_box) {
  box-sizing: border-box;
  height: 540px;
  width: 100%;
  margin: 0 auto;
  background-color: rgba(41, 41, 41, 0.781);
  position: relative;
  top: -520px;
  text-align: left;
  border: 5px solid white;
}

.work_hover p:not(.link_txt, .tech_txt) {
  position: relative;
  top: 15px;
  color: white;
  font-family: "ONE-Mobile-Title";
  font-style: normal;
  font-weight: lighter;
  font-size: 24px;
  line-height: 29px;
  left: 30px;
  margin-bottom: 2px;
  margin-top: 10px;
}

.work_hover .link_txt {
  margin-top: 11px;
  margin-bottom: 12px;
}

.link_box {
  position: relative;
  top: 60px;
  display: flex;
  width: 90%;
  height: 50%;
  margin: 0 auto;
}

.link_txt {
  color: white;
}

.link_box_detail {
  width: 49%;
  height: 82%;
  text-align: center;
  padding-top: 15%;
  padding-bottom: 10%;
  font-family: "ONE-Mobile-Title";
  font-style: normal;
  font-weight: lighter;
  font-size: 35px;
  line-height: 29px;
  color: white;
  box-sizing: border-box;
  border: 1px solid white;
  margin: 0 auto;
  text-decoration: none;
}

.link_box_detail:hover {
  background-color: rgba(189, 236, 236, 0.212);
}

.tech_txt {
  position: relative;
  top: 40px;
  color: white;
  font-family: "ONE-Mobile-Title";
  font-style: normal;
  font-weight: lighter;
  font-size: 24px;
  line-height: 29px;
  left: 30px;
  margin-bottom: 2px;
  margin-top: 20px;
}

.using_tech_box {
  position: relative;
  top: 40px;
  left: 20px;
  display: flex;
  flex-wrap: wrap;
}

.using_tech {
  color: white;
  text-align: center;
  margin-left: 10px;
  margin-top: 10px;
  width: 65px;
  border-radius: 30px;
  border: 2px solid white;
}

.a_button:not(.link_txt) {
  text-decoration: none;
  width: 100px;
  height: 50px;
  margin: 0 auto;
}

.a_button div:not(.link_txt) {
  width: 90px;
  height: 35px;
  border: 2px solid white;
  border-radius: 20px;
  text-align: center;
  font-size: 18px;
  line-height: 40px;
  color: white;
  margin: 0 auto;
}

.a_button :hover:not(.link_txt) {
  color: black;
  background-color: rgb(255, 255, 255);
  border: 2px solid black;
  border-radius: 20px;
}

.a_test {
  width: 50%;
  height: 50px;
}
