#standard {
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}

.video {
  width: 100%;
  height: 100%;
  background-color: white;
  object-fit: cover;
}

.profile {
  position: absolute;
  display: flex;
  width: 70%;
  height: 60%;
  border: 3px solid rgb(255, 255, 255);
  border-radius: 50px;
  left: 15%;
  top: 20%;
  z-index: 1;
}
.profile > p {
  color: white;
  box-sizing: border-box;
  text-align: center;
  font-size: 100px;
  width: 100%;
  height: 50%;
  margin: auto 0;
}
